export default {
  name: 'v_nav',
  props: {
    break_fund: {
      type: Function,
    },
  },
  data () {
    return {}
  },
  created(){

  },
  methods: {
    back(){
      if (this.break_fund) this.break_fund();
      else this.$router.go(-1);
    }
  }
}



